@font-face {
	font-family: 'SFCompactDisplay';
	src: url('./Fonts/FontsFree-Net-SFCompactDisplay-Regular.ttf') format('truetype'),
		url('./Fonts/FontsFree-Net-SFCompactDisplay-Medium.ttf') format('truetype'),
		url('./Fonts/FontsFree-Net-SFCompactDisplay-Bold.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');

body {
	overflow: hidden;
}

.nui-wrapper {
	text-align: center;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.tabletContainer {

	width: 95%;
	height: 95%;

	background: #272E41;
	border: 1px solid #313B50;
	border-radius: 2px;

	background-image: url('./Images/background.png');
	background-repeat: no-repeat;
	background-size: cover;

	display: flex;
	flex-direction: column;
	user-select: none;
}

.appList {

	display: flex;
	flex-direction: column;

	gap: 10px;

	margin-top: 32px;
	margin-left: 32px;

	.appItem {

		cursor: pointer;
		transition: all 0.1s;

		&:hover {
			background: rgba(255, 255, 255, 0.01)
		}

		display: flex;
		flex-direction: column;

		justify-content: center;
		align-items: center;

		width: 96px;
		height: 84px;

		gap: 7px;

		img {

			width: 48px;
			height: 48px;
			object-fit: contain;

		}

		div {

			font-family: 'SFCompactDisplay', sans-serif;
			font-style: normal;
			font-weight: 400;
			font-size: 12px;
			line-height: 14px;
			display: flex;
			align-items: center;
			text-align: center;
			letter-spacing: -0.01em;

			color: rgba(255, 255, 255, 0.8956);

		}

	}

}

.appContainer {

	position: absolute;
	width: 100%;
	height: 100%;

	left: 0;
	top: 0;

	display: flex;
	flex-direction: column;
	align-items: center;

	justify-content: center;

	.secBypass {

		display: flex;
		flex-direction: column;

		filter: drop-shadow(0px 32px 64px rgba(0, 0, 0, 0.37)) drop-shadow(0px 2px 21px rgba(0, 0, 0, 0.37));

		border: 1px solid rgba(117, 117, 117, 0.4);
		border-bottom: 0px solid;
		border-radius: 7px;

		width: 46%;
		height: 75%;

		background: url('./Images/ShellBase.png');

		.header {

			display: flex;
			flex-direction: row;

			padding: 16px 27px 16px 32px;

			.logoName {

				display: flex;
				flex-direction: row;
				align-items: center;

				gap: 15px;

				img {

					width: 32px;
					height: 32px;

				}

				span {

					font-family: 'SFCompactDisplay', sans-serif;
					font-style: normal;
					font-weight: 400;
					font-size: 19px;
					line-height: 23px;
					/* identical to box height */

					display: flex;
					align-items: center;
					letter-spacing: -0.01em;

					color: rgba(255, 255, 255, 0.8956);

				}

			}

			.appControls {

				display: flex;
				flex-direction: row;

				margin-left: auto;
				align-items: flex-end;
				margin-bottom: 10px;
				gap: 25px;

				svg {
					cursor: pointer
				}

			}

		}

		.loadingTextContainer {

			display: flex;
			flex-direction: column;
			align-items: flex-start;

			flex: 1;

			padding: 40px 37px 69px 32px;

			text-align: left;

			span {

				text-align: left;

				font-family: 'Oswald';
				font-style: normal;
				font-weight: 400;
				font-size: 18px;
				line-height: 27px;

				color: #FFFFFF;

				line-height: 38px;

			}


		}

		.hackContainer {

			display: flex;
			flex-direction: column;

			padding: 58px 68px 50px 68px;

			align-items: center;
			justify-content: center;

			.targetList {

				display: flex;
				flex-direction: row;

				span {

					font-family: 'Oswald';
					font-style: normal;
					font-weight: 400;
					font-size: 64px;
					line-height: 95px;

					color: #505256;

				}

			}

			.timer {

				font-family: 'Oswald';
				font-style: normal;
				font-weight: 400;
				font-size: 24px;
				line-height: 61px;

				letter-spacing: 0.035em;

				color: #FFFFFF;

			}

			.numberGrid {

				display: flex;
				flex-direction: row;

				flex-wrap: wrap;
				justify-content: center;

				span {

					font-family: 'Oswald';
					font-style: normal;
					font-weight: 400;
					font-size: 24px;
					line-height: 232.5%;

					letter-spacing: 0.035em;

					color: rgba(255, 255, 255, 0.45);

				}

			}

			.controls {

				font-family: 'Oswald';
				font-style: normal;
				font-weight: 400;
				font-size: 24px;
				line-height: 36px;

				color: #FFFFFF;

				margin-top: 37px;

			}

		}

	}

	.boostingApp {

		display: flex;
		flex-direction: column;

		filter: drop-shadow(0px 32px 64px rgba(0, 0, 0, 0.37)) drop-shadow(0px 2px 21px rgba(0, 0, 0, 0.37));

		border: 1px solid rgba(117, 117, 117, 0.4);
		border-bottom: 0px solid;
		border-radius: 7px;

		width: 65%;
		height: 65%;

		background: url('./Images/ShellBase.png');

		.header {

			display: flex;
			flex-direction: row;

			padding: 16px 27px 16px 32px;

			.logoName {

				display: flex;
				flex-direction: row;
				align-items: center;

				gap: 15px;

				img {

					width: 32px;
					height: 32px;

				}

				span {

					font-family: 'SFCompactDisplay', sans-serif;
					font-style: normal;
					font-weight: 400;
					font-size: 19px;
					line-height: 23px;
					/* identical to box height */

					display: flex;
					align-items: center;
					letter-spacing: -0.01em;

					color: rgba(255, 255, 255, 0.8956);

				}

			}

			.appControls {

				display: flex;
				flex-direction: row;

				margin-left: auto;
				align-items: flex-end;
				margin-bottom: 10px;
				gap: 25px;

				svg {
					cursor: pointer
				}

			}

		}

		.contentContainer {

			flex: 1;
			background: rgba(255, 255, 255, 0.03);
            overflow-x: hidden;
            overflow-y: scroll;
            scrollbar-gutter: stable both-edges;
            &::-webkit-scrollbar { 
                width: 3px;
            } 
            
            &::-webkit-scrollbar-track {} 
            
            &::-webkit-scrollbar-thumb { 
                background: rgba(255, 255, 255, 0.3);
            }

			.tabsContainer {

				background: rgba(255, 255, 255, 0.03);
				border-radius: 0px;

				padding: 24px 42px 0px 50px;

				display: flex;
				flex-direction: row;

				align-items: center;
				gap: 50px;

				.tabItem {

					display: flex;
					flex-direction: column;
					align-items: center;

					cursor: pointer;

					span {

						font-family: 'Oswald';
						font-style: normal;
						font-weight: 400;
						font-size: 18px;
						line-height: 27px;
						text-align: center;
						color: #FFFFFF;

						margin-bottom: 17px;

						&:not(.disabled):hover {
							background: linear-gradient(207.53deg, #8FD5CC -5.82%, #8FD5CC 179.66%);
							-webkit-background-clip: text;
							-webkit-text-fill-color: transparent;
							background-clip: text;
							text-fill-color: transparent;

							-webkit-text-stroke: 1px solid rgba(255, 255, 255, 0.2);

						}

						&.disabled {
							opacity: 0.45;
							cursor: not-allowed;
						}

					}

					.rectangle {
						background: linear-gradient(185.58deg, #1F3331 -69.99%, #8FD5CC 238.44%);
						border-radius: 4px;
						width: calc(100% + 10px);
						height: 1px;
					}

				}

				.queueContainer {

					font-family: 'Oswald';
					font-style: normal;
					font-weight: 400;
					font-size: 18px;
					line-height: 22px;

					color: #FFFFFF;

					display: flex;
					flex-direction: row;

					gap: 10px;

					.dots {

						display: flex;
						flex-direction: row;

						gap: 4px;

						span {

							background: linear-gradient(207.53deg, #1F3331 -5.82%, #8FD5CC 179.66%);
							border: 1px solid rgba(255, 255, 255, 0.2);

							width: 12px;
							height: 12px;

							border-radius: 50%;

							margin-top: 6.5px;

						}

					}

				}

				.rightFlex {

					display: flex;
					flex-direction: row;

					margin-left: auto;

					align-self: flex-start;

					gap: 25px;

				}

				.rank {

					text-transform: uppercase;

					font-family: 'Oswald';
					font-style: normal;
					font-weight: 400;
					font-size: 26px;
					line-height: 22px;
					letter-spacing: -0.01em;

					color: #FFFFFF;

				}

			}

			.title {

				font-family: 'Oswald';
				font-style: normal;
				font-weight: 400;
				font-size: 26px;
				line-height: 39px;
				letter-spacing: -0.01em;

				display: flex;
				text-transform: uppercase;

				color: #FFFFFF;

			}

			.vehicleList {

				display: flex;
				flex-direction: row;

				gap: 24px;
				margin-top: 16px;

				.vehicleItem {

					overflow: hidden;

					display: flex;
					flex-direction: column;

					background: rgba(255, 255, 255, 0.05);
					border: 1px solid rgba(255, 255, 255, 0.3);
					border-radius: 12px;

					flex: 1;

					.vehicleImage {

						position: relative;

						img {
							width: 100%;
							height: 100%;
						}

					}

					.soldSign {

						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						width: 50%;
						height: 50%;

					}

					.carInfo {

						display: flex;
						flex-direction: column;

						align-items: center;

						padding-top: 16px;
						padding-bottom: 16px;

						.carName {

							font-family: 'Oswald';
							font-style: normal;
							font-weight: 400;
							font-size: 22px;
							line-height: 33px;

							text-align: center;

							color: #FFFFFF;

						}

						.carDifficulty {

							font-family: 'Oswald';
							font-style: normal;
							font-weight: 400;
							font-size: 16px;
							line-height: 24px;

							text-align: center;

							color: rgba(255, 255, 255, 0.4);

						}

						.difficulty {

							display: flex;
							flex-direction: row;

							gap: 7px;

							margin-top: 8px;

						}

					}

					.line {

						background: rgba(217, 217, 217, 0.05);
						border-radius: 1px;

						height: 1px;
						width: 90%;

						align-self: center;

					}

					.bidInfo {

						align-items: center;

						display: flex;
						flex-direction: column;

						padding-top: 12px;
						padding-bottom: 16px;

						.bidLabel {

							font-family: 'Oswald';
							font-style: normal;
							font-weight: 400;
							font-size: 16px;
							line-height: 24px;

							color: rgba(255, 255, 255, 0.4);

						}

						.bidData {

							font-family: 'Oswald';
							font-style: normal;
							font-weight: 400;
							font-size: 16px;
							line-height: 24px;

							text-align: right;
							letter-spacing: -0.01em;

							color: #FFFFFF;

						}

					}

					.buttonArea {

						background: rgba(255, 255, 255, 0.05);
						border: 1px solid rgba(255, 255, 255, 0.05);
						border-radius: 0px 0px 12px 12px;

						padding: 13px;

						display: flex;
						justify-content: center;
						align-items: center;

						.biddingContainer {

							display: flex;
							flex-direction: row;

							align-items: center;

							gap: 6px;

							.bidInputBox {

								display: flex;
								flex-direction: row;

								background: rgba(255, 255, 255, 0.05);
								border: 1px solid rgba(255, 255, 255, 0.3);

								flex: 1;

								padding: 9px 9px 9px 15px;

								img {

									width: 22.14px;
									height: 24px;

								}

								input {

									font-family: 'Oswald';
									font-style: normal;
									font-weight: 400;
									font-size: 16px;
									line-height: 24px;
									letter-spacing: -0.01em;

									color: #FFFFFF;

									width: 100%;

								}

							}

						}

					}

				}

			}

		}

	}

	.blackmarketApp {

		display: flex;
		flex-direction: column;

		filter: drop-shadow(0px 32px 64px rgba(0, 0, 0, 0.37)) drop-shadow(0px 2px 21px rgba(0, 0, 0, 0.37));

		border: 1px solid rgba(117, 117, 117, 0.4);
		border-bottom: 0px solid;
		border-radius: 7px;

		width: 65%;
		height: 75%;

		background: url('./Images/ShellBase.png');

		.header {

			display: flex;
			flex-direction: row;

			padding: 16px 27px 16px 32px;

			.logoName {

				display: flex;
				flex-direction: row;
				align-items: center;

				gap: 15px;

				img {

					width: 32px;
					height: 32px;

				}

				span {

					font-family: 'SFCompactDisplay', sans-serif;
					font-style: normal;
					font-weight: 400;
					font-size: 19px;
					line-height: 23px;
					/* identical to box height */

					display: flex;
					align-items: center;
					letter-spacing: -0.01em;

					color: rgba(255, 255, 255, 0.8956);

				}

			}

			.appControls {

				display: flex;
				flex-direction: row;

				margin-left: auto;
				align-items: flex-end;
				margin-bottom: 10px;
				gap: 25px;

				svg {
					cursor: pointer
				}

			}

		}

	}

}

.repContainer {

	display: flex;
	flex-direction: column;

	gap: 10px;

	.repText {

		display: flex;
		flex-direction: row;
		align-items: center;

		gap: 10px;

		margin-left: auto;

		span {
			font-family: 'Oswald';
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;

			letter-spacing: -0.01em;

			color: #FFFFFF;
		}


	}

	.repBar {

		width: 100%;

		border: 1px solid rgba(255, 255, 255, 0.2);
		border-radius: 20px;
		box-sizing: border-box;

		height: 10px;

		.barInside {
			background: linear-gradient(269.92deg, #8FD5CC -5.57%, #1F3331 100.58%), url('./Images/overlay.png');
		}

	}

}

.tableContainer {

	display: flex;
	flex-direction: column;

	gap: 10px;

	margin-top: 20px;

	width: 100%;

	.headers {

		box-sizing: border-box;

		background: rgba(0, 0, 0, 0.2);
		border: 1px solid rgba(255, 255, 255, 0.2);

		display: flex;
		flex-direction: row;

		.item {

			display: flex;
			justify-content: center;
			align-items: center;

			font-family: 'Oswald';
			font-style: normal;
			font-weight: 400;
			font-size: 12px;
			line-height: 18px;
			letter-spacing: -0.01em;

			color: rgba(255, 255, 255, 0.4);

			box-sizing: border-box;
			border-right: 1px solid rgba(255, 255, 255, 0.2);

			flex: 1;

			padding-top: 11px;
			padding-bottom: 11px;

		}

	}

	.jobsList {

		box-sizing: border-box;

		display: flex;
		flex-direction: column;

		gap: 10px;

		.listEmpty {

			margin-top: 20px;

			font-family: 'Oswald';
			font-style: normal;
			font-weight: 400;
			font-size: 24px;
			line-height: 36px;

			letter-spacing: -0.01em;
			color: #FFFFFF;

			opacity: 0.5;

		}

		.jobItem {

			display: flex;
			flex-direction: row;

			border: 1px solid rgba(255, 255, 255, 0.2);

			.item {

				display: flex;
				justify-content: center;
				align-items: center;

				font-family: 'Oswald';
				font-style: normal;
				font-weight: 400;
				font-size: 18px;
				line-height: 27px;
				letter-spacing: -0.01em;

				color: #FFFFFF;

				box-sizing: border-box;
				border-right: 1px solid rgba(255, 255, 255, 0.2);

				flex: 1;

				padding-top: 15px;
				padding-bottom: 15px;

				div {

					display: flex;
					flex-direction: row;

				}

				.difficulty,
				.price {
					gap: 7px;
				}

			}

		}



	}

}

.button {

	justify-content: center;

	box-sizing: border-box;

	opacity: 0.6;
	border: 0.1px solid rgba(255, 255, 255, 0.45);

	cursor: pointer;

	padding-top: 12px;
	padding-bottom: 12px;

	text-transform: uppercase;

	&.pickup {
		background: linear-gradient(207.53deg, #1F3331 -5.82%, #8FD5CC 179.66%);
		width: 165px;
	}

	&.leavequeue {
		background: linear-gradient(207.53deg, #331F1F -5.82%, #D58F8F 179.66%);
		width: 165px;
	}

	&.kick {
		background: linear-gradient(207.53deg, #331F1F -5.82%, #D58F8F 179.66%);
		width: 90px;
	}

	&.disabled {
		opacity: 0.2;
		cursor: not-allowed;
	}

	&:not(.disabled):hover {
		opacity: 1;
	}


	font-family: 'Oswald';
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;

	display: flex;
	align-items: center;
	letter-spacing: -0.01em;

	color: #FFFFFF;

}

.promptContainer {

	position: absolute;

	width: 100%;
	height: 100%;

	left: 0;
	top: 0;

	display: flex;
	flex-direction: column;
	align-items: center;

	justify-content: center;

	.prompt {

		box-sizing: border-box;
		background: rgba(255, 255, 255, 0.05);

		border: 1px solid rgba(117, 117, 117, 0.4);
		box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.37);
		backdrop-filter: blur(24px);

		width: 420px;

		display: flex;
		flex-direction: column;

		text-align: left;

		.promptContent {

			gap: 4px;

			flex: 1;

			padding: 24px 32px;

			background: rgba(255, 255, 255, 0.05);
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

			display: flex;
			flex-direction: column;

			.text {

				font-family: 'Oswald';
				font-style: normal;
				font-weight: 400;
				font-size: 20px;
				line-height: 30px;

				color: white;

			}

			input {

				padding: 6px 16px;
				background: rgba(255, 255, 255, 0.28);
				border-radius: 4px;

				font-family: 'Oswald';
				font-style: normal;
				font-weight: 400;
				font-size: 22px;
				line-height: 33px;
				color: #FFFFFF;

			}

		}

		.titleBar {

			display: flex;
			flex-direction: row;

			align-items: center;

			padding: 11px 24px 17px 24px;

			span {
				font-family: 'Oswald';
				font-style: normal;
				font-weight: 500;
				font-size: 24px;
				line-height: 36px;

				color: #FFFFFF;
			}


			svg {

				opacity: 0.2;
				transition: opacity 0.1s;
				padding-top: 4px;

				&:hover {
					opacity: 1;
				}

				margin-left: auto;
				cursor: pointer;

				z-index: 1;

			}

		}

		.promptButtons {

			margin-top: 20px;

			display: flex;
			flex-direction: row;

			width: 100%;

			justify-content: space-between;

			.promptButton {

				transition: all 0.1s;

				cursor: pointer;

				&:hover {

					&.filled {
						background: rgba(255, 255, 255, 0.4);
					}

					&.outline {
						border: 1px solid rgba(255, 255, 255, 0.7);
					}

				}

				width: 162px;

				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;

				padding-top: 6px;
				padding-bottom: 6px;

				border-radius: 4px;

				font-family: 'Oswald';
				font-style: normal;
				font-weight: 400;
				font-size: 22px;
				line-height: 33px;

				color: #FFFFFF;

				text-transform: uppercase;

				z-index: 2;

				&.filled {
					background: rgba(255, 255, 255, 0.28);
				}

				&.outline {
					border: 1px solid rgba(255, 255, 255, 0.55);
				}

			}

		}

	}


}

.toolbar {

	display: flex;
	flex-direction: row;

	background: linear-gradient(0deg, rgba(32, 32, 32, 0.5), rgba(32, 32, 32, 0.5)), rgba(32, 32, 32, 0.96);
	background-blend-mode: normal, color, luminosity;

	backdrop-filter: blur(30px);

	margin-top: auto;
	height: 5.4%;

	align-items: center;

	.logo {

