// Firewall

.firewall-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-top: 20px;

	.fw-header {
		font-size: 28px;
	}

	.fw-timer {
		font-size: 22px;
		margin-top: 8px;
	}

	.fw-targetGround {
		margin-top: 45px;
		width: 600px;
		height: 450px;
		background: rgba(0,0,0,0.3);
		border-radius: 4px;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.fw-challengerNumber {
		line-height: 420px;
		width: 600px;
		height: 450px;
		font-size: 300px;
		position: absolute;
		color: rgba(4, 253, 16, 0.1);
		z-index: -1;
	}

	.fw-target {
		border-radius: 50%;
		background: #ee1040;
	}
}

.interception-container {
	position: relative;
	margin-top: 45px;
	width: 600px;
	height: 450px;
	background: rgba(0,0,0,0.3);
	border-radius: 4px;
	overflow: hidden;
}

.interception-container .falling-icon {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	font-size: 32px; /* Adjust the font size as needed */
	color: #ff0000; /* Adjust the icon color as needed */
	animation-name: fall;
	animation-duration: 2s; /* Adjust the animation duration as needed */
	animation-timing-function: linear;
	animation-fill-mode: forwards;
}

@keyframes fall {
	0% {
		top: 0;
	}
	100% {
		top: 100%; /* Adjust the final position as needed */
	}
}